<template>
    <div class="main_contact">
        <input class="main_contact-text" type="tel" placeholder="Ваш номер телефона" pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}" v-model="phone"/>
        <input class="main_contact-button" type="button" value="Заказать звонок" v-on:click="postPhone(phone)">
    </div>
</template>

<script>
    export default {
        name:'ContactForm',
        data(){
            return{
                phone: null
            }
        },
        methods:{
            postPhone(number){
               this.$root.$emit('send-phone',number)
            }
        }
        
    }
</script>

<style scoped>
    .main_contact{
        margin: auto;
        position: relative;
        width: max-content;
    }
    .main_contact input{
        border-radius: 3px;
        border: none;
        height: 40px;
        font-size: 14px;
    }
    .main_contact-button{
        position: absolute;
        right: 0;
        background:#FBD656;
        font-weight: 600;
    }
    .main_contact-text{
        width: 400px;
        padding: 14px;
        background:#F6F6F6
    }
    @media (max-width: 768px){
        .main_contact,.main_contact-text{
            width:100%
        }
    }
</style>