<template>
    <div id="filtersList" class="p-0">
        <PriceFilter/>
        <MaterialFilter :materialList="materials"/>
        <ManufactureFilter :manufactureList="manufacturers"/>
    </div>
</template>

<script>
    import PriceFilter from './PriceFilter'
    import ManufactureFilter from './ManufactureFilter'
    import MaterialFilter from './MaterialFilter'
    export default {
        name:'FiltersList',
        components:{
            PriceFilter,
            MaterialFilter,
            ManufactureFilter
        },
        props:["materials","manufacturers"]
    
    }
</script>

<style>
    
    #filtersList{
        width: 30%;
    }
    .filter h5:after{
        content: url('../../../assets/arrow.png');
        transition: transform 0.5s ease 50ms;
        transform: rotate(0);
        position: absolute;
        right: 3%;
    }
    .filter.hidden h5:after{
        transform: rotate(180deg);
        position: absolute;
        right: 3%;
    }
    .filter{
        position: relative;
        background: #F6F6F6;
        border-radius: 3px;
        display: flex;
        flex-direction: column;
        margin-bottom: 25px;
        overflow: visible;
        max-height: 100vh;
        transition: .5s ease max-height;
    }
    .filter h5{
        font-family: Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        height: 47px;
        display: flex;
        align-items: center;
        cursor: pointer;
    }
    .filter.hidden{
        max-height: 47px;
        overflow: hidden;
        transition: .5s ease max-height;
    }
    .vue-slider-rail{
        background: white;
    }

        .material-checkbox{
        position: absolute;
        z-index: -1;
        opacity: 0;
    }
    .material-checkbox+label {
        display: inline-flex;
        align-items: center;
        user-select: none;
    }
    .material-checkbox+label::before {
        content: '';
        display: inline-block;
        width: 1em;
        height: 1em;
        flex-shrink: 0;
        flex-grow: 0;
        border: 1px solid #adb5bd;
        border-radius: 0.25em;
        margin-right: 0.5em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50% 50%;
    }
    .material-checkbox:checked+label::before {
        border-color: #FBD656;;
        background-color: #FBD656;;
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
    }
    .material-checkbox:not(:disabled):not(:checked)+label:hover::before {
        border-color: #FBD656;
    }
    .material-checkbox:not(:disabled):active+label::before {
        background-color: #FBD656;
        border-color: #FBD656;
    }
    .material-checkbox:focus:not(:checked)+label::before {
        border-color: #FBD656;
    }
    @media (max-width: 1024px){
        #filtersList{
            width: 100%;
        }
    }
</style>