<template>
  <div id="app">
    <div class="app_overlay_success" :class="{show:isNumberSended}">
        <div class="app_overlay_success-message">
          <p>Cпасибо, ожидайте звонка</p>
          <input class="app_overlay_success-close" type="button" value="X" @click="isNumberSended=!isNumberSended">
        </div>
    </div>
    <Header/>
    <MainContent :productsData="products" :filterMaterials="materials" :filterManufacture="manufacturers"/>
    <Footer/>
  </div>
</template>

<script>
import Header from '@/components/Header'
import Footer from './components/Footer'
import MainContent from './components/MainContent'
import axios from 'axios'

export default {
  name: 'App',
  data(){
    return{
      products:null,
      materials: [],
      manufacturers:null,
      materialArray: null,
      manufacturersArray:null,
      isNumberSended:false,
      price:[0,600000],
      paginationBy: 6,
      url: `http://95.217.16.207:1337/products?`
    }
  },
  components:{
    Header,Footer,MainContent
  },
  methods:{
    filterByMaterial(materialArray){
      this.materialArray = materialArray
      this.filterByPrice(this.price)
    },
    filterByCountry(countryArray){
      this.manufacturersArray = countryArray
      this.filterByPrice(this.price)
    },
    async filterByPrice(priceArray){
      this.url = 'http://95.217.16.207:1337/products?'
      if(this.materialArray){
        this.materialArray.forEach(element => {
          this.url += `material.id=${element}&`
        });
      }
      if(this.manufacturersArray){
        this.manufacturersArray.forEach(element => {
          this.url += `manufacturer.id=${element}&`
        });
      }
      await axios
        .get(this.url)
        .then(response => (this.products = response.data));
      this.price = priceArray
      this.products = this.products.filter(product => product.price >= this.price[0] && product.price <= this.price[1])

    },
    postPhoneNumber(number){ 
      this.isNumberSended = !this.isNumberSended
      console.log(this.isNumberSended);
      axios
        .post('http://95.217.16.207:1337/forms',{
          body: number
        })
        .then({});
    }
  },
  async mounted(){
    const instance = axios.create();
    const getMaterials = async() =>{
      instance.interceptors.response.use(function (response) {
        response.data.forEach(async el => {
          el.count = 0
          await axios
            .get(`http://95.217.16.207:1337/products?material.id=${el.id}`)
            .then(dataMaterial => el.count = dataMaterial.data.length)
        })
        return response.data;
      });
       await instance
        .get('http://95.217.16.207:1337/materials')
        .then(response => this.materials = response)
    }

    getMaterials()

    this.$root.$on('filter-by-country', this.filterByCountry);
    this.$root.$on('filter-by-material', this.filterByMaterial);
    this.$root.$on('filter-by-price', this.filterByPrice);
    this.$root.$on('send-phone', this.postPhoneNumber);
    
    axios
      .get(`http://95.217.16.207:1337/products?`)
      .then(response => (this.products = response.data));
    axios
      .get('http://95.217.16.207:1337/manufacturers')
      .then(response => (this.manufacturers = response.data))
    
    
  }
}
</script>

