<template>
    <div id='price-filter' class="filter" v-bind:class="{hidden: isShown}">
        <h5  @click="isShown = !isShown" class="p-4">Цена (грн)</h5>
        <div class="px-4">
            <vue-slider v-model="value" 
                        :process-style="{ backgroundColor: '#FBD656' }"
                        :max="60000"
                        :tooltip="'none'"
                        >
                <template v-slot:dot="{ value, focus }">
                    <div :class="['custom-dot', { focus }]">
                        <div class="inner-circle"></div>
                    </div>
                </template>
            </vue-slider>
        </div>
        <div class="filter-range p-4">
            <input class="range-input ml-0 " v-model="value[0]" />
            <span class=" ">&#8212;</span>
            <input class="range-input" v-model="value[1]"/>
            <input class="range-input-button mr-0" type="button" value="Oк" v-on:click="filterByPrice(value)">
        </div>
    </div>
</template>

<script>
    import VueSlider from 'vue-slider-component'

    export default {
        name:'PriceFilter',
        data(){
            return{
                isShown: false,
                value:[500,45000]
            }
        },
        methods:{
            filterByPrice(priceArray){
                this.$root.$emit('filter-by-price',priceArray)
            }
        },
        components:{
            VueSlider
        }

    }
</script>

<style scoped>
    .filter-range{
        display: flex;
    }
    .filter-range span{
        flex:1
    }

    .filter-range>*{
        width: 100%;
        margin: 2%;
        flex: 4;
        text-align: center;
        min-width: 1px;
    }
    .filter-range>input:last-child{
        flex: 3;
    }

    .filter input{
        border:none;
        border-radius: 3px;
        height: 30px;
    }
    .range-input-button{
        background: #FBD656;
        border-radius: 3px;
    }
    .custom-dot {
        width: 100%;
        height: 100%;
        border-radius: 4px;
        background-color: black;
        transition: all .3s;
        display:flex;
        
    }
    .inner-circle{
        width: 25%;
        height: 25%;
        background: #FBD656;
        border-radius: 100%;
        margin:auto
    }

</style>