<template>
    <div id='manufacture-filter' class="filter" :class="{hidden: isShown}">
        <h5  @click="isShown = !isShown" class="p-4 " >Страна производитель</h5>
        <div class="px-4" v-for="item in manufactureList" :key="item.id">
            <input class="material-checkbox" type="checkbox" :id="item.seo_url" :name="item.seo_url" :value="item.id"  v-model="selectedCountries"  v-on:change="filterByCountry(selectedCountries)">
            <label :for="item.seo_url">{{item.title}}</label>
        </div>
    </div>
</template>

<script>
    export default {
        name:'ManufactureFilter',
        data(){
            return{
                isShown:true,
                selectedCountries:[]
            }
        },
        props:['manufactureList'],
        methods:{
            filterByCountry(countryArray){
                this.$root.$emit('filter-by-country',countryArray)
            }
        }
    }
</script>

