<template>
    <div id='material-filter' class="filter" v-bind:class="{hidden: isShown}">
        <h5  @click="isShown = !isShown" class="p-4">Материал</h5>
        <!-- <div class="px-4">
            <input type="checkbox" :name="All" :value="All" >
            <label for="vehicle1">Все</label>
        </div> -->
        <div class="px-4" v-for="item in materialList" :key="item.id">
            <input class="material-checkbox" type="checkbox" :id="item.seo_url" :name="item.seo_url" :value="item.id"  v-model="selectedMaterial"  v-on:change="filterByMaterial(selectedMaterial)">
            <label :for="item.seo_url">{{item.title}} ({{item.count}})</label>
        </div>
    </div>
</template>

<script>
    export default {

        name:'MaterialFilter',
        data(){
            return{
                isShown: false,
                selectedMaterial:[]
            }
        },
        props:['materialList'],
        methods:{
            filterByMaterial(materialArray){
                this.$root.$emit('filter-by-material',materialArray)
            }
        }
    }
</script>

